<template>
  <div class="HelloBoyMain">
    <div class="HelloBoyMain-header">
      <router-link to="/" exact>
        <div class="HelloBoyMain-logo"></div>
      </router-link>
      <router-link to="/" exact>
        <div class="HelloBoyMain-title">首页</div>
      </router-link>
      <router-link to="/AboutOurs" exact>
        <div class="HelloBoyMain-title">关于我们</div>
      </router-link>
      <router-link to="/ConnectionAuthor" exact>
        <div class="HelloBoyMain-title">联系程序猿</div>
      </router-link>
      <div class="HelloBoyMain-login">
        <router-link to="/Login" exact>
          <Button type="error" v-if="islogin==0" @click="showTabs('0')">登录</Button>
        </router-link>
        <router-link to="/Login" exact>
          <Button type="info" v-if="islogin==0" @click="showTabs('1')">注册</Button>
        </router-link>
        <Button type="warning" v-if="islogin==1" @click="exit">退出</Button>
      </div>
      <div class="nickname" v-if="islogin==1">{{userInfo.nickname}}</div>
      <div class="HelloBoyMain-img" v-if="islogin==1"></div>
    </div>
    <div>
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapState } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["islogin", "userInfo"])
  },
  methods: {
    exit() {
      this.$store.commit("UP_USERINFO");
      this.$store.commit("UP_ISLOGIN", 0);
    },
    showTabs(name) {
      this.$store.commit("UP_TABPANE_NAME", name);
    }
  },
  mounted() {}
};
</script>
<style lang="scss">
.HelloBoyMain {
  height: 100%;
  width: 100%;
  overflow: auto;
}

.HelloBoyMain .HelloBoyMain-header {
  height: 60px;
}

.HelloBoyMain-logo {
  float: left;
  height: 100%;
  margin-left: 150px;
  width: 150px;
  background: url(../assets/2018logo.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: top;
  cursor: pointer;
}
.HelloBoyMain-title {
  width: 100px;
  text-align: center;
  float: left;
  height: 60px;
  line-height: 60px;
  font-size: 15px;
  cursor: pointer;

  // font-weight:700;
}
.HelloBoyMain-login {
  float: right;
  height: 60px;
  line-height: 60px;
  margin-right: 60px;
}
.HelloBoyMain .router-link-active {
  font-weight: 700;
  color: #000;
}
.HelloBoyMain-img {
  margin: 10px 20px 10px 0px;
  height: 40px;
  width: 40px;
  background: url(../assets/2018logo.png);
  background-size: contain;
  border: 1.5px solid greenyellow;
  float: right;
  border-radius: 50%;
  cursor: pointer;
}
.HelloBoyMain .nickname {
  height: 60px;
  width: 150px;
  line-height: 60px;
  text-align: left;
  float: right;
  font-weight: 700;
  cursor: pointer;
  font-size: 15px;
}
</style>



